import Message from './message';

interface RequestMessageJson {
  request_id: string;
  name: string;
  meta: Record<string, string> | undefined;
  payload: Record<string, any>;
}

export default class RequestMessage extends Message {
  public toJson(): RequestMessageJson {
    return {
      request_id: this.id,
      name: this.name,
      meta: this.meta,
      payload: this.payload || {},
    };
  }

  public static isRequest(data: Record<string, any>): boolean {
    return !!data.request_id;
  }

  public static from(data: Record<string, any>): RequestMessage | undefined {
    if (data.name && data.request_id && data.payload) {
      const request = data as RequestMessageJson;

      return new RequestMessage(request.request_id, request.name, request.meta, request.payload);
    }
    return undefined;
  }
}
